.faq {
  color: #202020;

  .inner-container {
    display: grid;
    grid-template-columns: 25% 75%;

    .left {
      padding: 2rem;
      border-right: 1px solid rgb(236, 236, 236);

      .left-title {
        display: flex;
        align-items: center;

        .icon {
          display: flex;
          margin-right: 1rem;
        }

        h2 {
          color: #202020;
        }
      }

      .list {
        padding-left: 2rem;

        li {
          margin-top: 1rem;
          color: #383838;
        }
      }
    }

    .right {
      font-weight: 300;
      border-left: 1px solid rgb(236, 236, 236);
      padding-top: 2rem;
      padding-bottom: 2rem;
      padding-left: 2rem;
      padding-right: 4rem;

      .title {
        font-size: 1.2rem;
        font-weight: 400;
        margin-bottom: 1rem;
      }

      .accordians-content-container {
        .each-accordian-item {
          border-top: 1px solid #e8e8e8;
          padding-bottom: 1rem;
          padding-top: 1rem;

          .heading-title {
            display: flex;
            justify-content: space-between;

            span {
              &:last-child {
                font-size: 1.5rem;
              }
            }
          }

          .para {
            font-size: 0.9rem;
          }

          &:last-child {
            border-bottom: 1px solid #e8e8e8;
          }

          .accordion__panel {
            margin-top: 1.5rem;
          }
        }
      }
    }
  }
}

[data-whatinput="mouse"] :focus,
[data-whatinput="touch"] :focus {
  outline: none;
}

.accordion__button:focus {
  font-weight: bold;
  outline: none;
}
